.profile__avatar-overlay {
    background-color: rgba(0,0,0,.8);
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: visibility 0.2s, opacity 0.2s linear;
}

.profile__avatar:hover .profile__avatar-overlay {
    opacity: 1;
    visibility: visible;
}